* {
  box-sizing: border-box;

}
/* this is the font and background color of the body */
body {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  margin: 0;
  background: rgb(0, 84, 120);
}

/* this consists of the nav and header sectionv*/

header {
  color: lightskyblue;
  font-size: 10px;
}

nav {
  background-color: lightblue;

}



.name-nav {
  color: darkslategray;
  font-family: Lucida;
  text-shadow: -1px 1px 2px #000;
  text-decoration: none;
  display: flex;
  padding: 20px;
  justify-content: space-between;
  align-items: center;

  /* this is the about me section */
}

.banner-background {
  background-image: url(./images/banner-background.jpeg);
  background-position: center;
  background-size: cover;
  height: 250px;
  max-width: 100%;
  margin: 0px;
  object-fit: fill;
}

.figure-aboutme {
  font-family: Arial, Helvetica, sans-serif;
  margin:auto;
  padding: 30px;
}

/* this is the project section with boxes */

.project {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color: blanchedalmond;
  border-style: solid;
  border-radius: 10px;
  border-width: 15px;
  margin: auto;
  height: 300px !important;
}

.box {
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  align-items: flex-end;
  background-color: none;
  color: none;

  padding: 10px;
  margin: 20px;
  height: 200px;


}

.project1 {
  /* 
  background-image: url(./images/Project1.jpeg);
background: stretch; */

  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.project1 img {
  width: 100%;

  
}

/* This is what makes the boxes opaque and get darker when you hover over it*/


.project1 img{
  border-style: solid;
  border-width: 20px;
  border-radius: 10px;
  transition: all 0.5s ease-in-out;
  opacity: 0.5;
}
.project1 img:hover {
  opacity: 1;
}
.project1 img:hover{
  box-shadow: inset 0px 0px 15px lightblue, 0 0 25px lightskyblue;
}

.box1 img:hover{
  box-shadow: inset 0px 0px 15px lightblue, 0 0 25px lightskyblue;
}
.box2 img:hover{
  box-shadow: inset 0px 0px 15px lightblue, 0 0 25px lightskyblue;
}

.box1 img {
  height: 300px;
  width: 50px;
  border-style: solid;
  border-width: 20px;
  border-radius: 10px;
  transition: all 0.5s ease-in-out;
  opacity: 0.5;

}

.box1 img:hover {
  opacity: 1;
}

.box2 img {
  height: 400px;
  width:90%;
 
  border-style: solid;
  border-width: 20px;
  border-radius: 10px;
  transition: all 0.5s ease-in-out;
  opacity: 0.5;
}

.box2 img:hover {
  opacity: 1;
}

.box1,
.box2 {
  height: 400px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.img-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

}
