header{
  display:flex;
  align-items: center;
  justify-content: space-between;
}
nav ul{
  display:flex;
  align-items: center;
  justify-content: space-around;
  list-style-type: none; /* Remove bullets */

}
nav ul a {
  text-decoration: none;
  color:white;
  font-weight:600;
}

figure {
  margin-top: -250px;
}

figure img {
  border-radius: 100px;
}


header{
  height:50px;
  /* padding:20px; */
  background-color:rgb(7, 1, 49);
}
header nav{
  /* padding: 10px; */
  background-color: rgb(12, 116, 243);
  width:30%;
}
header .name{
  /* padding: 10px; */
  background-color: rgb(12, 116, 243);
  
}
nav ul li a {
   opacity: 0.8;
   transition:1s;
}
nav ul li a:hover{
  opacity:1;
