}

.selected {
  background-color: rgb(0, 153, 255);
  padding: 10px;
}

body{
  background-color: rgb(86, 110, 108);
}
.banner{
  background-image: url("https://cdn4.tropicalsky.co.uk/images/1800x600/lush-coastal-scenery-in-hawaii.jpg");
  background-size:cover;
  height: 400px;
  display:flex;
  align-items: flex-end;
  justify-content: flex-end;
}
.quote{
padding:15px;
color:white;
background-color:rgba(0,0,0,0.3);
border-radius:10px;
font-family: Arial, Helvetica, sans-serif;
}
main {
  display:flex;
  align-items: stretch;
  justify-content: space-around;
  height:100%;
}
.left-section{
  width:20%;
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  color:white;
  text-decoration: underline;
  border-right:5px solid white;
  
}
.right-section{
padding:20px;
  width:50%;
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.projects h3{
padding:15px;
color:white;
background:rgba(0,0,0,0.3);
margin-bottom:-55px;
width:90%;
text-align:center;

border-top-left-radius: 20px;
border-top-right-radius: 20px;
}
.project1 h3{
  width:100%;
}
.contacts-links{
  display:flex;
  align-items:center;
  justify-content: space-around;
  width:100%;
}
.img-box{
margin:10px;
}
.box2{
  margin-top:30px;
  margin-bottom: 90px;
}

.small-screen{
  display:none;
} 


.box1,.box2,.project1{
  width: 100%;

  
}
.contacts-links{
  height:400px;
}
.left{
  flex-grow: 0;  
     /* do not grow   - initial value: 0 */
  flex-shrink: 0;   
  /* do not shrink - initial value: 1 */
   flex-basis: 200px;
 margin:20px;
  border-right:4px solid white;
  align-self: stretch;
}  

.left h3{
  text-decoration: underline;
  color:white;
}

.div1,.div2,.div3{
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.navbar-box{
  display:none;
}

.floating {
  display: flex;
  flex-direction: row;
  justify-content: space-around;

}

@media(max-width:780px){
 
  .project1 h3{
     width:100%;
  }
     .left{
         display:none;
     }
    figure{
     width:100%;
       margin:20px;
 
    }
    figure img{
     display:block;
     margin:auto;
    }
    header{
     flex-direction: column-reverse;
     height:120px;
    }
    header nav{
     align-self: stretch;
